export const GET_AGENTS = ()=>{
    return (dispatch:any, getState:any)=>{
        axios.post(API_AGENTS_LIST_URL, {

        }, {
            headers: {
                "Authorization": "Bearer "+getState().auth.token
            }
        })
        .then((res)=>{
            if(res.status === 200){
                const data:any = res.data;
                if(data.success === true){
                    dispatch({type: SET_DATA, payload:{data: data.data.data.data}})
                }
            }
        })
        .catch((e)=>{
            console.log(e);
        });
    };
};



export const ADD_AGENT = ()=>{
    return (dispatch:any, getState:any)=>{
        var bodyFormData = new FormData();
        bodyFormData.append('email', getState().agents.email);
        bodyFormData.append('username', getState().agents.username);
        bodyFormData.append('firstName', getState().agents.firstName);
        bodyFormData.append('lastName', getState().agents.lastName);
        bodyFormData.append('gender', getState().agents.gender);
        bodyFormData.append('contact1', getState().agents.contact.replaceAll("(", "").replaceAll(")", "").replaceAll(" ", "").replaceAll("-", ""));
        bodyFormData.append('address', getState().agents.address);
        bodyFormData.append('userType', getState().agents.userType);
        bodyFormData.append('agentSource', getState().agents.agentSource);
        axios.post(API_AGENTS_ADD_URL, bodyFormData, {
            headers: {
                "Authorization": "Bearer "+getState().auth.token
            }
        })
        .then((res)=>{
            if(res.status === 200){
                const data:any = res.data;
                if(data.success === true){
                    dispatch({type: SET_DATA, payload:{data: data.data.data.data}})
                }
            }
        })
        .catch((e)=>{
            console.log(e);
        });
    };
};

