import * as React from 'react';
import { useDispatch, useSelector } from "react-redux";
import Toolbar from '@mui/material/Toolbar';
import Paper from '@mui/material/Paper';
import Card from '@mui/material/Card';
import Box from '@mui/material/Box';
import { IMaskInput } from 'react-imask';
import OutlinedInput from '@mui/material/OutlinedInput';
import Typography from '@mui/material/Typography';
import CardContent from '@mui/material/CardContent';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import FormErrorComponent from '../../../../../Layouts/FormErrorComponent';

import DashboardAgents from '../Agents';

import PaperHeadingContainer from '../../../../Layouts/PaperHeadingContainer';

import {FormErrorSingleComponent} from '../../../../../Layouts/FormErrorComponent';

import {
  validateUsername,
  validateEmail,
  validateFirstName,
  validateLastName,
  validateGender,
  validateContact,
  validateAddress,
  validateUserType,
} from '../../../../../lib/FormValidationComponent';

import {
  ADD_AGENT,
  SET_EMAIL,
  SET_USERNAME,
  SET_EMAIL_VALID,
  SET_EMAIL_NOT_VALID,
  SET_FIRST_NAME,
  SET_LAST_NAME,
  SET_GENDER,
  SET_CONTACT,
  SET_ADDRESS,
  SET_USER_TYPE,
  SET_USERNAME_VALID,
  SET_USERNAME_NOT_VALID,
  SET_FIRST_NAME_VALID,
  SET_FIRST_NAME_NOT_VALID,
  SET_LAST_NAME_VALID,
  SET_LAST_NAME_NOT_VALID,
  SET_AGENT_SOURCE,
  SET_GENDER_VALID,
  SET_GENDER_NOT_VALID,
  SET_CONTACT_VALID,
  SET_CONTACT_NOT_VALID,
  SET_ADDRESS_VALID,
  SET_ADDRESS_NOT_VALID,
  SET_USER_TYPE_VALID,
  SET_USER_TYPE_NOT_VALID,
} from "../../state/agents";

interface CustomProps {
  onChange: (event: { target: { name: string; value: string } }) => void;
  name: string;
}

const TextMaskCustom = React.forwardRef<HTMLElement, CustomProps>(
  function TextMaskCustom(props, ref) {
    const { onChange, ...other } = props;
    return (
      <IMaskInput
        {...other}
        mask="(#0) 000-000-0000"
        definitions={{
          '#': /[1-9]/,
        }}
        onAccept={(value: any) => onChange({ target: { name: props.name, value } })}
        overwrite
      />
    );
  },
);

interface State {
  textmask: string;
}

function DashboardAddAgent(props:any) {
  const dispatch = useDispatch();
  const state = useSelector((state:any)=>state.agents); 
  
  const [loaded, setLoaded] = React.useState<boolean>(false);
  React.useEffect(()=>{
    setLoaded(true);
    console.log("state", state)
  }, [])
  return (
    <div>

      <DashboardAgents
        breadcrumbs={[
          {id:1, text: 'Dashboard', href:'/'},
          {id:2, text: 'Agents', href:'/dashboard/agents'},
          {id:3, text: 'Add'}
        ]}
      >

      {loaded === true ?
          <PaperHeadingContainer heading={"Add Agents"}>
          <Grid item xs={12} md={12} lg={12} sx={{m:1}}>
            <FormErrorSingleComponent error={(state.email.length > 0) && !state.isValidEmail} msg={"Please provide a valid email address."}>
              <TextField
              sx={{mb: 1}}
                id="email"
                label="Email"
                type="text"
                fullWidth
                error={(state.email.length > 0) && !state.isValidEmail}
                maxRows={4} 
                value={state.email}
                onChange={(e:any)=>{
                  if(validateEmail(e.target.value)){
                    dispatch({type:SET_EMAIL_VALID});
                  }
                  else{
                    dispatch({type:SET_EMAIL_NOT_VALID});
                  }
                  dispatch({type:SET_EMAIL, payload: {email: e.target.value}});
                  return e;
                }}
              />
            </FormErrorSingleComponent>

            <FormErrorSingleComponent error={(state.username.length > 0) && !state.isValidUsername} msg={"Please provide a valid username."}>
              <TextField
              sx={{mb: 1}}
                id="username"
                label="Username"
                type="text"
                fullWidth
                error={(state.username.length > 0) && !state.isValidUsername}
                maxRows={4} 
                value={state.username}
                onChange={(e:any)=>{
                  if(validateUsername(e.target.value)){
                    dispatch({type:SET_USERNAME_VALID});
                  }
                  else{
                    dispatch({type:SET_USERNAME_NOT_VALID});
                  }
                  dispatch({type:SET_USERNAME, payload: {username: e.target.value}});
                  return e;
                }}
              />
            </FormErrorSingleComponent>

            <FormErrorSingleComponent error={(state.firstName.length > 0) && !state.isValidFirstName} msg={"Please provide a valid username."}>
              <TextField
              sx={{mb: 1}}
                id="first_name"
                label="First Name"
                type="text"
                fullWidth
                maxRows={4} 
                value={state.firstName}
                onChange={(e:any)=>{
                  if(validateFirstName(e.target.value)){
                    dispatch({type:SET_FIRST_NAME_VALID});
                  }
                  else{
                    dispatch({type:SET_FIRST_NAME_NOT_VALID});
                  }
                  dispatch({type:SET_FIRST_NAME, payload: {firstName: e.target.value}});
                  return e;
                }}
              />
            </FormErrorSingleComponent>

            <FormErrorSingleComponent error={(state.lastName.length > 0) && !state.isValidLastName} msg={"Please provide a valid username."}>
              <TextField
              sx={{mb: 1}}
                id="last_name"
                label="Last Name"
                type="text"
                fullWidth
                maxRows={4} 
                value={state.lastName}
                onChange={(e:any)=>{
                  if(validateLastName(e.target.value)){
                    dispatch({type:SET_LAST_NAME_VALID});
                  }
                  else{
                    dispatch({type:SET_LAST_NAME_NOT_VALID});
                  }
                  dispatch({type:SET_LAST_NAME, payload: {lastName: e.target.value}});
                  return e;
                }}
              />
            </FormErrorSingleComponent>

            <FormErrorSingleComponent error={(state.agentSource.length > 0) && !state.isValidAgentSource} msg={"Please provide a valid username."}>
              <TextField
              sx={{mb: 1}}
                id="agent_source"
                label="Agent Source"
                type="text"
                fullWidth
                maxRows={4} 
                value={state.agentSource}
                onChange={(e:any)=>{
                  dispatch({type:SET_AGENT_SOURCE, payload: {lastName: e.target.value}});
                  return e;
                }}
              />
            </FormErrorSingleComponent>

            <FormErrorSingleComponent error={(state.gender.length > 0) && !state.isValidGender} msg={"Please provide a valid username."}>
              <Box sx={{ minWidth: 120 }}>
                <FormControl fullWidth>
                  <InputLabel id="gender-label">Gender</InputLabel>
                  <Select
                    labelId="gender-label"
                    id="gender"
                    value={state.gender}
                    label="Gender"
                    onChange={(e:any)=>{
                      if(validateGender(e.target.value)){
                        dispatch({type:SET_GENDER_VALID});
                      }
                      else{
                        dispatch({type:SET_GENDER_NOT_VALID});
                      }
                      dispatch({type:SET_GENDER, payload: {gender: e.target.value}});
                    }}
                  >
                    <MenuItem value={"male"}>Male</MenuItem>
                    <MenuItem value={"female"}>Female</MenuItem>
                  </Select>
                </FormControl>
              </Box>
            </FormErrorSingleComponent>

            <FormErrorSingleComponent error={(state.contact.length > 0) && !state.isValidContact} msg={"Please provide a valid contact."}>

              <FormControl sx={{ width: '100%' }} variant="outlined">
                <InputLabel id="contact-label">Contact</InputLabel>
                <OutlinedInput
                  id="contact"
                  label="Contact"
                  value={state.contact}
                  onChange={(e:any)=>{
                    if(validateContact(e.target.value)){
                      dispatch({type:SET_CONTACT_VALID});
                    }
                    else{
                      dispatch({type:SET_CONTACT_NOT_VALID});
                    }
                    dispatch({type:SET_CONTACT, payload: {contact: e.target.value}});
                    return e;
                  }}
                  inputComponent={TextMaskCustom as any}
                />
              </FormControl>

            </FormErrorSingleComponent>




            <FormErrorSingleComponent error={(state.address.length > 0) && !state.isValidAddress} msg={"Please provide a valid address."}>
              <TextField
              sx={{mb: 1}}
                id="address"
                label="Address"
                type="text"
                fullWidth
                maxRows={4} 
                value={state.address}
                onChange={(e:any)=>{
                  if(validateAddress(e.target.value)){
                    dispatch({type:SET_ADDRESS_VALID});
                  }
                  else{
                    dispatch({type:SET_ADDRESS_NOT_VALID});
                  }
                  dispatch({type:SET_ADDRESS, payload: {address: e.target.value}});
                  return e;
                }}
              />
            </FormErrorSingleComponent>

            <FormErrorSingleComponent error={(state.userType.length > 0) && !state.isValidUserType} msg={"Please provide a valid user type."}>
              <Box sx={{ minWidth: 120 }}>
                <FormControl fullWidth>
                  <InputLabel id="user-type-label">User Type</InputLabel>
                  <Select
                    labelId="user-type-label"
                    id="user-type"
                    value={state.userType}
                    label="User Type"
                    onChange={(e:any)=>{
                      if(validateUserType(e.target.value)){
                        dispatch({type:SET_USER_TYPE_VALID});
                      }
                      else{
                        dispatch({type:SET_USER_TYPE_NOT_VALID});
                      }
                      dispatch({type:SET_USER_TYPE, payload: {userType: e.target.value}});
                      return e;
                    }}
                  >
                    <MenuItem value={3}>Calling Agent</MenuItem>
                    <MenuItem value={4}>Sub Agent</MenuItem>
                  </Select>
                </FormControl>
              </Box>
            </FormErrorSingleComponent>

            <div style={{display: "flex", justifyContent:"center", margin: '20px'}}>
              <Button 
                variant="contained" 
                disabled={!(
                  (state.email.length > 0) && 
                  (state.username.length > 0) && 
                  validateEmail(state.email) && 
                  (state.firstName.length > 0) && 
                  (state.lastName.length > 0) && 
                  (state.address.length > 0) && 
                  (state.gender !== "") && 
                  (state.userType !== "") && 
                  (state.contact !== "")
                )}
                onClick={(e)=>{
                  dispatch(ADD_AGENT())
                }}  
              >Submit</Button>
            </div>
            

          </Grid>
          </PaperHeadingContainer>
        :
        null
      }
      </DashboardAgents>

    </div>
  );
}

export default DashboardAddAgent;
